import { Droppable } from 'react-beautiful-dnd';
import { Label } from 'semantic-ui-react';
import styled from 'styled-components'
import { CourseModel } from '../../../models/CourseModel';
import { TermModel } from '../../../models/TermModel';
import CourseView from '../CourseView/CourseView';
import { ICourseGroup, ICurrentDraggableProps } from '../PlannerViewModel';
import { MenuItemProps } from 'semantic-ui-react'
import memoizeOne from "memoize-one"
import { IMultiSelectProps, IContextMenuProps } from '../Planner';

const Container = styled.div`
  margin: 8px;
  border: 1px solid lightgrey;
  border-radius:2px;

  display: flex;
  flex-direction: column;
  flex-grow: 1;
  ${props => !props.horizontal ? "max-width: 350px" : ""};
  ${props => props.title === "Planned" ? "min-width: 350px" : ""};
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none;          /* Likely future */ 
`

const Title = styled.h3`
  padding: 6px;
  margin: 0 0;
  display: flex;
  width: 200px;
`;

const CourseList = styled.div`
  padding: 6px;
  min-height: 100px;
  transition: background-color 0.2s ease;
  background-color: ${props =>
  (props.isDraggingOver
    ? props.isValidDroppable || props.isMultiSelectDragging
      ? '#8debb7'
      : '#ff99b0'
    : props.isPossibleTermOfCurrentDraggable
      ? '#cafae0'
      : (props.courseID === "completed")
        ? '#edf2f4'
        : (props.courseID === "planned")
          ? '#edf2f4'
          : '#edf2f4'
  )
  };
  flex-grow: 1;
  display: flex;
  flex-direction: ${props => props.horizontal ? "row" : "column"};
  flex-flow: row ${props => props.horizontal ? "wrap" : "nowrap"};
  align-content: flex-start;
  max-width: inherit;
`;



const TermHeader = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
`

const UocLabel = styled(Label)`
  && {
    margin: auto 0 auto 0;
    height: fit-content;
    vertical-align: middle;
    white-space: nowrap;
  } 
`

interface ITermViewProps {
  id: string
  title: string
  courses: CourseModel[]
  term: TermModel | null
  currentDraggableProps: ICurrentDraggableProps
  horizontal?: boolean

  registerDraggableRef: (courseModel: CourseModel, ref: React.MutableRefObject<any>) => void
  deregisterDraggableRef: (courseModel: CourseModel) => void

  multiSelect: IMultiSelectProps;
  contextMenu: IContextMenuProps;
  isRegionSelecting: boolean;
}

const getSelectedMap = memoizeOne((selectedCourses: CourseModel[]) =>
  selectedCourses.reduce((previous, current) => {
    previous[current.id] = true;
    return previous;
  }, {}),
);

const CourseGroupView = (props: ITermViewProps) => {
  const isPossibleTerm = (term: TermModel | null): boolean => {
    return props.currentDraggableProps.possibleTerms.filter(_term => _term.equals(term)).length > 0
  }
  const totalUoc = props.courses.map(course => course.creditPoints).reduce((a, b) => a + b, 0)

  return (
    <Container horizontal={props.horizontal} title={props.title}>
      <TermHeader>
        <Title>{props.title}</Title>
        {
          props.term
            ? <UocLabel
              color={(totalUoc === 18 || props.courses.length === 3)
                ? "green"
                : totalUoc < 18
                  ? "orange"
                  : "yellow"
              }
              content={`${totalUoc} UOC`}
              size={"small"}
            />
            : <></>
        }
      </TermHeader>
      <Droppable
        droppableId={props.id}
        direction={props.horizontal ? 'horizontal' : 'vertical'}
      >
        {
          (provided, snapshot) => (
            <CourseList
              horizontal
              {...provided.droppableProps}
              ref={provided.innerRef}
              isDraggingOver={snapshot.isDraggingOver}
              courseID={provided.droppableProps['data-rbd-droppable-id']}
              isPossibleTermOfCurrentDraggable={isPossibleTerm(props.term)}
              isValidDroppable={props.term === null || isPossibleTerm(props.term)}
              isMultiSelectDragging={props.multiSelect.selectedCourses.length > 1}
            >
              {
                props.courses ? props.courses.map((course, index) => {
                  const isSelected: boolean = !!getSelectedMap(props.multiSelect.selectedCourses)[course.id]
                  const isGhosting: boolean = isSelected && !!props.multiSelect.draggingCourseId && props.multiSelect.draggingCourseId !== course.id;
                  return (
                    <CourseView
                      horizontal={props.horizontal}
                      key={course.code}
                      course={course}
                      droppableIndex={index}
                      prerequisitesOfCurrentDraggable={props.currentDraggableProps.prerequisiteCourseIds}
                      isSelected={isSelected}
                      isGhosting={isGhosting}
                      registerDraggableRef={props.registerDraggableRef}
                      deregisterDraggableRef={props.deregisterDraggableRef}
                      multiSelect={props.multiSelect}
                      contextMenu={props.contextMenu}
                      isRegionSelecting={props.isRegionSelecting}
                    />
                  )
                })
                  : undefined
              }
              {provided.placeholder}
            </CourseList>
          )
        }
      </Droppable>
    </Container>
  )
};
export default CourseGroupView
