import { Observer, observer } from 'mobx-react';
import React, { useEffect, useCallback, useRef, useState } from 'react';
import styled from 'styled-components'
import { DragDropContext, DragStart, DropResult } from 'react-beautiful-dnd';
import { TermModel } from '../../models/TermModel';
import { ICourseGroup, PlannerViewModel } from './PlannerViewModel';
import CourseGroupView from './CourseGroupView/CourseGroupView';
import YearView from './YearView/YearView';
import { CourseModel, CourseStatus } from '../../models/CourseModel';
import { Menu, MenuItemProps, Popup } from 'semantic-ui-react';
import { Box, boxesIntersect, useSelectionContainer } from 'react-drag-to-select'

const Container = styled.div`
  display: flex;
  flex-direction: row;
`

const YearPlanner = styled.div`
  display: inherit;
  overflow: auto;
`

export interface IMultiSelectProps {
  selectedCourses: CourseModel[];
  draggingCourseId?: string;
  toggleSelection: (course: CourseModel) => void;
  toggleSelectionInGroup: (course: CourseModel) => void;
}

export interface IContextMenuProps {
  getContextMenuItems: (course: CourseModel) => MenuItemProps[];
  isAnyContextOpen: boolean;
  setIsAnyContextOpen: (b: boolean) => void;
  currentOpenContextStateSetter: React.MutableRefObject<any>;
}

const Planner = observer(({ plannerViewModel }: { [key: string]: PlannerViewModel }) => {
  return <Observer>
    {() => {
      useEffect(() => {
        window.addEventListener("keydown", onWindowKeyDown);
        window.addEventListener("contextmenu", onWindowContextMenu);
        window.addEventListener("dragenter", onDragEnter);
        window.addEventListener("dragexit", onDragExit);
        window.addEventListener("mouseup", onMouseUp);

        return () => {
          window.removeEventListener("keydown", onWindowKeyDown);
          window.removeEventListener("contextmenu", onWindowContextMenu);
          window.removeEventListener("dragenter", onDragEnter);
          window.removeEventListener("dragexit", onDragExit);
          window.removeEventListener("mouseup", onMouseUp);
        }
      });

      const courseState = plannerViewModel.courseState;
      const plannerState = plannerViewModel.plannerState;

      // Drag region selection
      const [isRegionSelecting, setIsRegionSelecting] = useState(false);
      const draggableItemRef = useRef<Map<CourseModel, HTMLDivElement>>(new Map<CourseModel, HTMLDivElement>());
      const draggableBoxRef = useRef<[CourseModel, Box][]>([]);

      const registerDraggable = (courseModel: CourseModel, ref: React.MutableRefObject<HTMLDivElement | null | undefined>) => {
        if (!ref.current) return;
        draggableItemRef.current?.set(courseModel, ref.current);
      }

      const deregisterDraggable = (courseModel: CourseModel) => {
        draggableItemRef.current?.delete(courseModel);
      }

      const handleSelectionChange = useCallback((box: Box) => {
        draggableBoxRef.current
          .filter(([courseModel, draggableBox]) => {
            const isSelected: boolean = plannerViewModel.selectedCourses.includes(courseModel);
            const isIntersecting: boolean = boxesIntersect(box, draggableBox);

            return (isSelected !== isIntersecting);
          }, [])
          .forEach(([courseModel, _]) => toggleSelectionInGroup(courseModel));
      }, []);

      const { DragSelection } = useSelectionContainer({
        onSelectionStart: () => {
          setIsRegionSelecting(true);
          draggableBoxRef.current = [...draggableItemRef.current.entries()].reduce((previous: [CourseModel, Box][], [courseModel, div]) => {
            const rect = div.getBoundingClientRect();
            return [
              ...previous,
              [courseModel, rect]
            ];
          }, []);
        },
        onSelectionChange: handleSelectionChange,
        onSelectionEnd: () => {
          draggableBoxRef.current = [];
        }
      });

      // Dragging
      const [isDragging, setIsDragging] = React.useState(false);
      const onDragStart = (initial: DragStart) => {
        setIsDragging(true);
        const selected: CourseModel | undefined = plannerViewModel.selectedCourses.find(
          (course: CourseModel): boolean => course.id === initial.draggableId,
        );

        if (!selected) plannerViewModel.unselectAllCourses();

        plannerViewModel.draggingCourseId = initial.draggableId;

        const course = courseState.getCourseById(initial.draggableId);

        if (!course) return;

        // Don't calculate validations if we are multi selecting
        if (plannerViewModel.selectedCourses.length > 1) return;

        plannerViewModel.currentDraggableProps = {
          ...plannerViewModel.currentDraggableProps,
          possibleTerms: plannerViewModel.calculateDisplayableTerms().filter(term => {
            return courseState.getMissingRequirementsWhenAssigningCourseToTerm(course, term).length === 0
          }),
          prerequisiteCourseIds: new Set<string>(courseState.getFlattenedPrerequisiteList(course.prerequisites))
        }
      }

      const onDragEnd = (result: DropResult) => {
        const { destination, source } = result;
        if (!destination || result.reason === "CANCEL") {
          plannerViewModel.draggingCourseId = undefined;
          return;
        }
        if (!plannerViewModel.draggingCourseId) return;

        plannerViewModel.selectedCourses.forEach((course: CourseModel) =>
          plannerViewModel.setCourseByDroppableId(course, source.droppableId, destination.droppableId)
        );

        const allGroups = plannerViewModel.getAllGroups();
        allGroups.forEach((group: ICourseGroup) => {
          group.courses = group.courses.filter((courseInGroup: CourseModel) =>
            !plannerViewModel.selectedCourses.find((selectedCourse: CourseModel) =>
              selectedCourse.id === courseInGroup.id
            )
          )
        })
        plannerViewModel.spliceByGroupId(destination.droppableId, destination.index, 0, ...plannerViewModel.selectedCourses)

        const draggingCourseIsSelected: boolean = !!plannerViewModel.selectedCourses.find((courseModel) => {
          return courseModel.id === plannerViewModel.draggingCourseId;
        });
        if (!draggingCourseIsSelected) {
          const courseModel = courseState.getCourseById(plannerViewModel.draggingCourseId);
          if (courseModel) {
            plannerViewModel.setCourseByDroppableId(courseModel, source.droppableId, destination.droppableId);
            plannerViewModel.spliceByGroupId(source.droppableId, source.index, 1);
            plannerViewModel.spliceByGroupId(destination.droppableId, destination.index + plannerViewModel.selectedCourses.length, 0, courseModel);
          }
        }

        plannerViewModel.draggingCourseId = undefined;

        setIsDragging(false);

        plannerViewModel.clearCurrentDraggableProps()
      }

      const onBeforeCapture = () => {
        plannerViewModel.currentDraggableProps = {
          ...plannerViewModel.currentDraggableProps,
          isDragging: true,
        }
      }

      // Multi select
      const onWindowKeyDown = (event: KeyboardEvent) => {
        if (event.defaultPrevented) return;

        if (event.key === "Escape") plannerViewModel.unselectAllCourses();
      }

      const onMouseUp = (event: MouseEvent) => {
        if (isAnyContextOpen) return;

        if (isDragging) return;

        event.preventDefault();

        if (isRegionSelecting) {
          setIsRegionSelecting(false);
          return;
        }

        if (event.button !== 0) return;
        if (event.ctrlKey) return;

        plannerViewModel.unselectAllCourses();
      }

      const onDragEnter = (event) => {
        event.preventDefault();
      }

      const onDragExit = (event) => {
        event.preventDefault();
      }

      const toggleSelection = (course: CourseModel) => {
        const selectedCourses = plannerViewModel.selectedCourses;
        const wasSelected: boolean = selectedCourses.includes(course);

        const newCourses = (() => {
          // Task not previously selected or part of the selected group, now the only selected item
          if (!wasSelected || selectedCourses.length > 1) {
            return [course];
          }

          // Task previously selected but not in a group, clear the selection
          return [];
        })();

        plannerViewModel.selectedCourses = newCourses;
      }

      const toggleSelectionInGroup = (course: CourseModel) => {
        const selectedCourses = plannerViewModel.selectedCourses;
        const index: number = selectedCourses.indexOf(course);

        // if not selected - add it to the selected items
        if (index === -1) {
          plannerViewModel.selectedCourses = [...selectedCourses, course];
          return;
        }

        // it was previously selected and now needs to be removed from the group
        const newCourses: CourseModel[] = [...selectedCourses];
        newCourses.splice(index, 1);
        plannerViewModel.selectedCourses = newCourses;
      };

      // Context Menu
      const getContextMenuItems = (course?: CourseModel) => {
        const areMultipleItems = plannerViewModel.selectedCourses.length > 1;
        let menuItems: MenuItemProps[] = [
          {
            key: 'remove',
            content: `Remove ${areMultipleItems ? "all courses" : "course"}`,
            icon: 'delete calendar',
            onClick: () => {
              const moveSelectedToRemoved = () => {
                plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setUnplanned());
                plannerViewModel.unselectAllCourses();
              }
              if (!course) {
                moveSelectedToRemoved();
              } else {
                if (plannerViewModel.selectedCourses.includes(course)) moveSelectedToRemoved();
                course.setUnplanned();
              }
            }
          },
          {
            key: 'plan',
            content: `Move ${areMultipleItems ? "all courses" : "course"} to planned`,
            icon: 'bookmark',
            onClick: () => {
              const moveSelectedToPlanned = () => {
                plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setPlanned());
              }
              if (!course) {
                moveSelectedToPlanned();
              } else {
                if (plannerViewModel.selectedCourses.includes(course)) moveSelectedToPlanned();
                course.setPlanned();
              }
            }
          },
          {
            key: 'complete',
            content: `Move ${areMultipleItems ? "all courses" : "course"} to completed`,
            icon: 'checked calendar',
            onClick: () => {
              const moveSelectedToCompleted = () => {
                plannerViewModel.selectedCourses.forEach((courseModel) => courseModel.setCompleted());
              }
              if (!course) {
                moveSelectedToCompleted();
              } else {
                if (plannerViewModel.selectedCourses.includes(course)) moveSelectedToCompleted();
                course.setCompleted();
              }
            }
          },
        ];
        if (course) {
          if (course.courseStatus === CourseStatus.UNPLANNED) {
            menuItems.splice(0, 1);
          } else if (course.courseStatus === CourseStatus.PLANNED) {
            menuItems.splice(1, 1);
          } else if (course.courseStatus === CourseStatus.COMPLETED) {
            menuItems.splice(2, 1);
          }
        }
        return menuItems;
      }

      const [isAnyContextOpen, setIsAnyContextOpen] = React.useState(false);
      const [isWindowContextOpen, setIsWindowContextOpen] = React.useState(false);

      const currentOpenContextStateSetter = React.useRef<React.Dispatch<React.SetStateAction<boolean>>>();

      const contextRef = React.useRef<any>();

      const onWindowContextMenu = (event) => {
        if (event.defaultPrevented) return;

        // Don't use context when no items selected. Possibly change this.
        if (plannerViewModel.selectedCourses.length === 0) return;

        event.preventDefault();

        if (isAnyContextOpen && currentOpenContextStateSetter.current) {
          currentOpenContextStateSetter.current(false);
        }

        currentOpenContextStateSetter.current = setIsWindowContextOpen;

        contextRef.current = {
          getBoundingClientRect: () => ({
            left: event.clientX,
            top: event.clientY,
            right: event.clientX + 1,
            bottom: event.clientY + 1,
            height: 0,
            width: 0,
          }),
        }
        setIsAnyContextOpen(true)
        setIsWindowContextOpen(true)
      }

      const multiSelectProps: IMultiSelectProps = {
        selectedCourses: plannerViewModel.selectedCourses,
        draggingCourseId: plannerViewModel.draggingCourseId,
        toggleSelection: toggleSelection,
        toggleSelectionInGroup: toggleSelectionInGroup,
      };

      const contextMenuProps: IContextMenuProps = {
        getContextMenuItems: getContextMenuItems,
        isAnyContextOpen: isAnyContextOpen,
        setIsAnyContextOpen: setIsAnyContextOpen,
        currentOpenContextStateSetter: currentOpenContextStateSetter,
      }

      return (
        <React.Fragment>
          <DragSelection />
          <Popup
            basic
            context={contextRef}
            onClose={() => {
              setIsAnyContextOpen(false)
              setIsWindowContextOpen(false)
            }}
            open={isWindowContextOpen}
          >
            <Menu
              items={getContextMenuItems()}
              onItemClick={(e) => {
                e.preventDefault();
                setIsAnyContextOpen(false)
                setIsWindowContextOpen(false)
              }}
              secondary
              vertical
            />
          </Popup>
          <DragDropContext
            onBeforeCapture={onBeforeCapture}
            onDragStart={onDragStart}
            onDragEnd={onDragEnd}
          >
            {
              <CourseGroupView
                horizontal={true}
                key={plannerState.completedGroup.id}
                id={plannerState.completedGroup.id}
                title={plannerState.completedGroup.title}
                term={null}
                courses={plannerState.completedGroup.courses}
                currentDraggableProps={plannerViewModel.currentDraggableProps}
                multiSelect={multiSelectProps}
                contextMenu={contextMenuProps}
                isRegionSelecting={isRegionSelecting}
                registerDraggableRef={registerDraggable}
                deregisterDraggableRef={deregisterDraggable}
              />
            }
            <Container>
              {
                <CourseGroupView
                  horizontal={false}
                  key={plannerState.plannedGroup.id}
                  id={plannerState.plannedGroup.id}
                  title={plannerState.plannedGroup.title}
                  term={null}
                  courses={plannerState.plannedGroup.courses}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  multiSelect={multiSelectProps}
                  contextMenu={contextMenuProps}
                  isRegionSelecting={isRegionSelecting}
                  registerDraggableRef={registerDraggable}
                  deregisterDraggableRef={deregisterDraggable}
                />
              }
              <YearPlanner>
                {
                  plannerState.yearGroups.map(yearGroup => {
                    return <YearView
                      id={yearGroup.id}
                      title={yearGroup.title}
                      year={yearGroup.year}
                      courseGroups={yearGroup.courseGroups}
                      currentDraggableProps={plannerViewModel.currentDraggableProps}
                      multiSelect={multiSelectProps}
                      contextMenu={contextMenuProps}
                      isRegionSelecting={isRegionSelecting}
                      registerDraggableRef={registerDraggable}
                      deregisterDraggableRef={deregisterDraggable}
                    />
                  })
                }
              </YearPlanner>
            </Container>
            {
              plannerViewModel.currentDraggableProps.isDragging
                ?
                <CourseGroupView
                  horizontal={true}
                  key={"trash"}
                  id={"trash"}
                  title={"Remove"}
                  term={null}
                  courses={[]}
                  currentDraggableProps={plannerViewModel.currentDraggableProps}
                  multiSelect={multiSelectProps}
                  contextMenu={contextMenuProps}
                  isRegionSelecting={isRegionSelecting}
                  registerDraggableRef={registerDraggable}
                  deregisterDraggableRef={deregisterDraggable}
                />
                : <></>
            }
          </DragDropContext>
        </React.Fragment>
      )
    }}
  </Observer >
})

export default Planner;
